
/****************custom properties CSS**************************/

:root {
  /*
  original color
  --first-color:rgb(217,0,98);
  --first-alpha-color:rgb(217,0,98,0.75);*/

  --first-color:rgb(255, 255, 255);
  --first-alpha-color:rgb(217,0,98,0.75);

  --second-color:rgb(232, 97, 7);
  --second-alpha-color:rgba(232, 97, 7,0.75);

  --third-color:rgb(12, 5, 54);
  --third-alpha-color:rgb(80,20,100,0.75);

  --link-color:rgb(80,158,227);
  --link-alpha-color:rgb(80,158,227,0.75);
  --bg-color:rgb(250, 245, 245);
  --bg-alpha-color:rgb(245,245,245,0.75);

  --black-alpha-color:rgba(0,0,0,0.5);


  --red-color:rgb(253, 124, 124);

  --hero-opacity-color:rgba(0,0,0,0.5);

}



/********************Reset styles************************/

body {
  font-family: "Raleway",sans-serif;
  background-color: var(--bg-alpha-color);
}

a {
  color: var(--link-color);
  transition: all .3s ease-out;
}
a:hover {
  color: var(--first-alpha-color);
}

html {
  scroll-behavior: smooth !important;
}




/*********************Bootstrap style********************/

.navbar-brand img {
  width: 1rem;
  height: 1rem;

}

.navbar-brand {
  padding: 0;
}

.bg-dark {
  background-color: var(--third-color) !important; 
}

.nav-item {
  
  transition: all .3s ease-out;

}

.navbar-nav {
  flex-direction: row;
}

.nav-item:hover {
  background-color: var(--first-alpha-color);

}

.nav-link {
  padding: 0;
  color: var(--first-color);
  font-size: 1.15rem;
  font-weight: bold;

}

.nav-link:hover {
  color: var(--first-alpha-color);
}

.dropdown-item {
  color: var(--first-alpha-color);
  
  font-weight: bold;
}

.dropdown-item:hover {
  color: var(--third-alpha-color);
  
  
}
.card-img-top {
