}
.btn-text-carousel{
  font-size: 2vw;
}
.i-agg-carousel{
  font-size: 2vw;
  
  height: auto;
  color: white;
}
.btn-num-pedido{
  background-color: red;
  border-radius: 2rem;
  margin-left: 0.2rem;
  padding: 0.1rem;
  width: 1.5rem;

  
}

.btn-num{
  font-size: 3vw;
  
